'use strict';

import { isIE } from './ieError';
import { clickElement } from './clickElement';
import { clickClose } from './clickClose';
import { loadShowContent } from './load';

{
  /**
   * ie ブラウザで開いたときにエラーを表示
   */
  isIE();
}


/*
 * btnにclickのする要素のid名
 * getIdに追加するクラス名
*/

const content: contentMap = {
  resume: {
    btn: 'js-btnResume',
    getId: 'resume'
  },
  work: {
    btn: 'js-btnWork',
    getId: 'work'
  },
  blog: {
    btn: 'js-btnBlog',
    getId: 'blog'
  }
};

interface contentMap {
  [key: string]: {
    [btn: string]: string;
  }
};

{
  Object.keys(content).forEach((key) => {
    new clickElement(content[key]['btn'], content[key]['getId'], 'is-show').showContent();
  });
  new clickElement('js-topBtn', 'js-topMenu', 'is-none').showTop();
  new clickElement('js-topBtn', 'js-content', 'is-show').showTop();
  new clickElement('js-topBtn', 'js-menu', 'is-none').showMenu();
  new clickElement('js-topBtn', 'js-topName', 'is-rotation').rotationTopName();
}

{
  /**
   * @param  {} content
   * nav メニューの閉じるボタンをクリックイベント
   */
  clickClose(content)
}

{
  loadShowContent();
}
