const btnClose = document.getElementById('js-btnCloseContent');
const topMenu = document.getElementById('js-topMenu');
const topNames = Array.from(document.getElementsByClassName('js-topName'));
const menu = document.getElementById('js-menu');
const contentBlock = document.getElementById('js-content');

// あとでgetにする
function removeClass(elem: any, className: string) {
  if (elem !== null) {
    elem.classList.remove(className);
  }
}

function addClass(elem: any, className: string) {
  if (elem !== null) {
    elem.classList.add(className);
  }
}
/**
 * @param  {any} obj
 * @returns void
 * closeボタンをクリックしたときのイベント
 */
export function clickClose(obj: any): void {
  let url = location.pathname
  let path = url.split('/')

  if (btnClose !== null) {
    btnClose.onclick = () => {
      /**
       * @param  {} path[1] === ''
       * TOPページかを判定
       * URLのパス名で配列にし、配列の長さが1のときはTOP
       * TOP(/)であれば[""]
       * 下層(/blog)であれば["blog"]となる
       */
      if(path[1] === '') {
        removeClass(topMenu, 'is-none')

        for (const topName of topNames) {
          const elem = <HTMLElement>topName;
          elem.classList.remove('is-rotation')
        }

        addClass(menu, 'is-none')

        removeClass(contentBlock, 'is-show')
        Object.keys(obj).forEach((key) => {
          let elemRemove = document.getElementById(obj[key]);

          removeClass(elemRemove, 'is-show')
        });
      } else {
        window.location.href = '/'
      }
    }
  }
}
