let hash = location.hash
let getId = document.getElementById(hash.replace('#', ''))


let topMenu = document.getElementById('js-topMenu')
let content = document.getElementById('js-content')

let menuId = document.getElementById('js-menu')

let rotations = Array.from(document.getElementsByClassName('js-topName'));

export function loadShowContent() {
  let elemAddClasses = document.getElementsByClassName('js-container');
  if (hash) {
    window.onload = () => {
      /**
       * @param  {} elemAddClasses
       * @param  {} .forEach((elem
       * showContent()
       */
      Object.values(elemAddClasses).forEach((elem) => {
        elem.classList.add('is-none');
        elem.classList.remove('is-show');
        getId!.classList.remove('is-none');
        getId!.classList.add('is-show');
      });

      topMenu!.classList.add('is-none');
      content!.classList.add('is-show');
      /**
       * @param  {} 'is-none'
       * showMenu()
       */
      menuId!.classList.remove('is-none');

      for (const rotation of rotations) {
        const elem = <HTMLElement>rotation;
        elem.classList.add('is-rotation');
      }
    }
  }
}
